.dash,.work,.asset,.pm,.space,.fm,.tenant
{
    color: black;
    font-size: 7px;
    display: flex;
    align-items: center;
    
}
.dashdec
{
    padding-top: 30px;
}

.img
{
    width: 15px;
    height: 15px;
    margin-right: 10px;
}
.dashcon
{
    margin-top: 60px;
    margin-left: 30px;
}
.settingdec
{
margin-top: 250px;
}
.setting
{
    color: black;
    font-size: 8px;
    display: flex;
    align-items: center;
    margin-left: 30px;
}
.task
{
    margin-left: 18px;
    color: black;
    font-size: 10px;
    width: 10rem;
    height:6.2rem;
    background-color: rgb(110, 109, 109);
    border-radius: 10px;
    padding-top: 5px;
    margin-bottom: 50px;
}
.taskbtn
{
    border-radius: 20px;
    
    font-size: 12px;
    
    border-color: aquamarine;
}
.taskbtn::first-letter
{
   
    font-size: 20px;
    padding-right: 10px;
}

   
