{
    width: 57%;
    display: flex;
    font-size: 8px;
    
    gap: 10px;
    
}
.con2card1
{
    width: 65%;
    display: flex;
    flex-direction: column;
    margin-left: 20px;
    border-radius: 10px;
    background-color: white;
    color: #666666;
    
}
