{
    text-align: left;
    margin-left: 30px;
    color: #666666;
    font-size: 18px;
}
.con2titledec
{
    width: 67%;
    display: flex;
    justify-content: space-between;

}
.occupied
{
    display: flex;
    align-items: center;
}
.occupied img
{
    width: 100px;
    height: 100px;
    margin-left: 5px;
    margin-right: 10px;
    margin-top: 5px;
}
.con2card1con
{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    margin-top: 40px;

}
.con2card1con span
{
font-size: 13px;
color: black;
}
.con2card2
{
    width: 46%;
    display: flex;
    flex-direction: column;

    gap: 10px;  
}
.booking
{
    width: 95%;
padding-top: 37px;
    background-color: white;
    border-radius: 10px;
    
}
.booking h2
{
    font-size: 14px;
    margin-top: 5px;
    margin-left: 4px;
}
.con2card3
{
    width: 47%;
   
    display: flex;
    flex-direction: column;
    background-color: white;
    border-radius: 10px;
    padding-left: 20px;
    margin-right: 20px;
}
.latency img
{
    width: 10px;
    height: 10px;
    margin-left: 6px;

}
.latency
{
    font-size: 14px;
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.latency button
{
    color: #666666;
    height: 30px;
    padding: 5px 10px 5px 10px;
    margin-top: 10px;
    border-radius: 5px;
    border:2px solid #666666;
}
.latency h3
{
    margin-left: -30px;
}


